@use "base/variables" as v;
@use "base/mixins" as m;

.iconos {
  margin-top: 3rem;
  .listado-iconos {
    @include m.tablet {
      @include m.grid(3, 5rem);
    }
    li {
      img {
        width: 12rem;
        margin: 0 auto;
      }
      p {
        font-family: v.$fuenteCursiva;
        font-size: 3.2rem;
        color: v.$primario;
      }
    }
  }
}
